<template>
  <div class="subpage">
    <Row class="card-box-row">
      <Col
        v-for="item in indicators1"
        :key="item.text"
        :span="24 / indicators1.length"
      >
        <Indicator v-bind="item"/>
      </Col>

      <Col :lg="10">
        <Card>
          <p slot="title">GDP产业占比分析</p>
          <Echart class="chart" :option="chart1"/>
        </Card>
      </Col>
      <Col :lg="14">
        <Card>
          <p slot="title">GDP总量全镇分布</p>
          <Echart class="chart" :option="chart2"/>
        </Card>
      </Col>

      <Col :lg="10">
        <Card>
          <p slot="title">当年季度GDP趋势分析</p>
          <Echart class="chart" :option="chart3"/>
        </Card>
      </Col>
      <Col :lg="14">
        <Card>
          <p slot="title">产业GDP趋势分析</p>
          <Echart class="chart" :option="chart4"/>
        </Card>
      </Col>

      <Col
        v-for="item in indicators2"
        :key="item.text"
        :span="24 / indicators2.length"
      >
        <Indicator v-bind="item"/>
      </Col>

      <Col :lg="10">
        <Card>
          <p slot="title">规模以上工业企业资产总量占比分析</p>
          <Echart class="chart" :option="chart5"/>
        </Card>
      </Col>
      <Col :lg="14">
        <Card>
          <p slot="title">规模以上工业企业总量趋势分析</p>
          <Echart class="chart" :option="chart6"/>
        </Card>
      </Col>

      <Col :lg="10">
        <Card>
          <p slot="title">进出口总值占比分析</p>
          <Echart class="chart" :option="chart7"/>
        </Card>
      </Col>
      <Col :lg="14">
        <Card>
          <p slot="title">进出口总值趋势分析</p>
          <Echart class="chart" :option="chart8"/>
        </Card>
      </Col>
    </Row>
  </div>
</template>

<script>
import Echart from '@/components/Echart'
import Indicator from '@/components/Indicator'
import {
  createArray
} from '@/utils/common'
import {
  createData,
  resolveGrowthRate
} from '@/utils/chart'
import {
  toFixed
} from '@/utils/number'

export default {
  components: {
    Echart,
    Indicator
  },
  data() {
    return {
      indicators1: [
        {
          color: '#2d8cf0',
          icon: './icon/year.png',
          text: '当年GDP',
          value: '106.21亿元'
        },
        {
          color: '#ed3f14',
          icon: './icon/quarter.png',
          text: '当季GDP',
          value: '26.18亿元'
        },
        {
          color: '#19be6b',
          icon: './icon/tongbi.png',
          text: '季度同比',
          value: '6%'
        },
        {
          color: '#9839ca',
          icon: './icon/huanbi.png',
          text: '季度环比',
          value: '8%'
        }
      ],
      chart1: {
        tooltip: {
          trigger: 'item',
          formatter: '{b}: {c} ({d}%)'
        },
        series: [
          {
            type: 'pie',
            radius: [0, '75%'],
            label: {
              formatter: '{b}',
              position: 'inner',
              emphasis: {
                textStyle: {
                  fontSize: 20
                }
              }
            },
            data: [
              {
                value: 28171,
                name: '第一产业（农业）'
              },
              {
                value: 18592,
                name: '第二产业（工业）'
              },
              {
                value: 25893,
                name: '第三产业（服务业）'
              }
            ]
          },
        ]
      },
      chart2: () => {
        const data = [
          createArray(15, i => toFixed(2 * (Math.random() * 0.2 + 1), 2)),
          createArray(15, i => toFixed(3 * (Math.random() * 0.2 + 1), 2)),
          createArray(15, i => toFixed(3 * (Math.random() * 0.2 + 1), 2))
        ]
        const sum = data.reduce((sum, nums) => sum + nums.reduce((sum, num) => sum + num, 0), 0)
        return {
          legend: {
            x: 'center',
            y: 'top',
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'shadow'
            }
          },
          grid: {
            left: 20,
            right: 70,
            bottom: '3%',
            containLabel: true
          },
          yAxis: [
            {
              name: '亿元',
              type: 'value',
            },
            {
              name: '百分比',
              type: 'value',
            }
          ],
          xAxis: {
            type: 'category',
            data: [
              '石碣社区',
              '唐洪村',
              '水南村',
              '石碣村',
              '刘屋村',
              '横滘村',
              '鹤田厦村',
              '四甲村',
              '沙腰村',
              '梁家村村',
              '桔洲村',
              '单屋村',
              '涌口村',
              '西南村',
              '黄泗围村'
            ],
          },
          series: [
            {
              name: '农业',
              type: 'bar',
              stack: '总量',
              label: {
                show: true,
                formatter: '{a}',
              },
              data: data[0]
            },
            {
              name: '工业',
              type: 'bar',
              stack: '总量',
              label: {
                show: true,
                formatter: '{a}',
              },
              data: data[1]
            },
            {
              name: '服务业',
              type: 'bar',
              stack: '总量',
              label: {
                show: true,
                formatter: '{a}',
              },
              data: data[2]
            },
            {
              name: 'GDP总量占比',
              type: 'line',
              yAxisIndex: 1,
              data: createArray(15, i => toFixed(data.reduce((sum, nums) => nums[i] + sum, 0) / sum * 100, 2))
            }
          ]
        }
      },
      chart3: () => {
        const data = createData(5, 25, 0.2, 0.05).map(n => n.toFixed(2))
        return {
          legend: {
            x: 'center',
            y: 'top',
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'shadow'
            }
          },
          grid: {
            left: 20,
            right: 70,
            bottom: '3%',
            containLabel: true
          },
          yAxis: [
            {
              name: '亿元',
              type: 'value',
            },
            {
              name: '百分比',
              type: 'value',
            }
          ],
          xAxis: {
            type: 'category',
            data: [
              '第一季度',
              '第二季度',
              '第三季度',
              '第四季度'
            ],
          },
          series: [
            {
              name: 'GDP',
              type: 'bar',
              data: data.slice(1)
            },
            {
              name: '环比',
              type: 'line',
              yAxisIndex: 1,
              data: resolveGrowthRate(data)
            },
            {
              name: '同比',
              type: 'line',
              yAxisIndex: 1,
              data: createData(4, 5, 3, 0.01).map(n => n.toFixed(2))
            }
          ]
        }
      },
      chart4: () => {
        const data = {
          'GDP农业': createData(5, 25, 0.1, 0.05).map(n => +n.toFixed(2)),
          'GDP工业': createData(5, 35, 0.1, 0.05).map(n => +n.toFixed(2)),
          'GDP服务业': createData(5, 40, 0.1, 0.05).map(n => +n.toFixed(2)),
        }
        return {
          legend: {
            x: 'center',
            y: 'top'
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'shadow'
            }
          },
          grid: {
            left: 20,
            right: 70,
            bottom: '3%',
            containLabel: true
          },
          yAxis: [
            {
              name: '亿元',
              type: 'value',
            },
            {
              name: '增长率',
              type: 'value',
            }
          ],
          xAxis: {
            type: 'category',
            data: [
              '2015',
              '2016',
              '2017',
              '2018'
            ],
          },
          series: [
            {
              name: '农业',
              type: 'bar',
              barGap: 0,
              label: {
                show: true,
                formatter: '{a}',
              },
              data: data['GDP农业'].slice(1)
            },
            {
              name: '工业',
              type: 'bar',
              label: {
                show: true,
                formatter: '{a}',
              },
              data: data['GDP工业'].slice(1)
            },
            {
              name: '服务业',
              type: 'bar',
              label: {
                show: true,
                formatter: '{a}',
              },
              data: data['GDP服务业'].slice(1)
            },
            {
              name: '农业增长率',
              type: 'line',
              yAxisIndex: 1,
              data: resolveGrowthRate(data['GDP农业'])
            },
            {
              name: '工业增长率',
              type: 'line',
              yAxisIndex: 1,
              data: resolveGrowthRate(data['GDP工业'])
            },
            {
              name: '服务业增长率',
              type: 'line',
              yAxisIndex: 1,
              data: resolveGrowthRate(data['GDP服务业'])
            }
          ]
        }
      },
      indicators2: [
        {
          color: '#7275ed',
          icon: './icon/assets.png',
          text: '规模以上工业企业资产总量',
          value: '46.98亿元'
        },
        {
          color: '#ea9a27',
          icon: './icon/assets+.png',
          text: '规模以上工业企业资产增长率',
          value: '6%'
        },
        {
          color: '#9dc71b',
          icon: './icon/io.png',
          text: '进出口总值',
          value: '49.16亿美元'
        },
        {
          color: '#2dc9f0',
          icon: './icon/io+.png',
          text: '进出口增长率',
          value: '18%'
        }
      ],
      chart5: {
        tooltip: {
          trigger: 'item',
          formatter: '{b}: {c} ({d}%)'
        },
        series: [
          {
            name: '类型',
            type: 'pie',
            radius: ['25%', '85%'],
            roseType: 'area',
            label: {
              formatter: '{b}: {c}亿元 ({d}%)',
            },
            data: [
              {
                value: 16.68,
                name: '工业'
              },
              {
                value: 13.85,
                name: '商业'
              },
              {
                value: 17.63,
                name: '其他'
              }
            ]
          },
        ]
      },
      chart6: () => {
        const data = {
          'GDP农业': createData(6, 25, 0.1, 0.05).map(n => +n.toFixed(2)),
          'GDP工业': createData(6, 35, 0.1, 0.05).map(n => +n.toFixed(2)),
          'GDP服务业': createData(6, 40, 0.1, 0.05).map(n => +n.toFixed(2)),
          '规模以上农业': createData(6, 10, 0.1, 0.05).map(n => +n.toFixed(2)),
          '规模以上工业': createData(6, 20, 0.1, 0.05).map(n => +n.toFixed(2)),
          '规模以上服务业': createData(6, 25, 0.1, 0.05).map(n => +n.toFixed(2)),
        }
        return {
          legend: {
            x: 'center',
            y: 'top',
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'shadow'
            }
          },
          grid: {
            left: 20,
            right: 70,
            bottom: '3%',
            containLabel: true
          },
          yAxis: [
            {
              name: '数量',
              type: 'value',
            },
            {
              name: '增长率',
              type: 'value',
            }
          ],
          xAxis: {
            type: 'category',
            data: [
              '2014',
              '2015',
              '2016',
              '2017',
              '2018'
            ],
          },
          series: [
            {
              name: '农业',
              type: 'bar',
              stack: '总量',
              label: {
                show: true,
                formatter: '{a}',
              },
              data: data['规模以上农业'].slice(1)
            },
            {
              name: '工业',
              type: 'bar',
              stack: '总量',
              label: {
                show: true,
                formatter: '{a}',
              },
              data: data['规模以上工业'].slice(1)
            },
            {
              name: '服务业',
              type: 'bar',
              stack: '总量',
              label: {
                show: true,
                formatter: '{a}',
              },
              data: data['规模以上服务业'].slice(1)
            },
            {
              name: '农业增长率',
              type: 'line',
              yAxisIndex: 1,
              data: resolveGrowthRate(data['规模以上农业'])
            },
            {
              name: '工业增长率',
              type: 'line',
              yAxisIndex: 1,
              data: resolveGrowthRate(data['规模以上工业'])
            },
            {
              name: '服务业增长率',
              type: 'line',
              yAxisIndex: 1,
              data: resolveGrowthRate(data['规模以上服务业'])
            }
          ]
        }
      },
      chart7: {
        tooltip: {
          trigger: 'item',
          formatter: '{b}: {c}亿美元 ({d}%)'
        },
        series: [
          {
            type: 'pie',
            radius: [0, '40%'],
            label: {
              formatter: '{b}',
              position: 'inner',
              emphasis: {
                textStyle: {
                  fontSize: 20
                }
              }
            },
            data: [
              {
                value: 14.25,
                name: '进口'
              },
              {
                value: 32.66,
                name: '出口'
              }
            ]
          },
          {
            type: 'pie',
            radius: ['50%', '60%'],
            label: {
              formatter: '{b}: {c}亿美元 ({d}%)'
            },
            data: [
              {
                value: 3.47,
                name: '农业'
              },
              {
                value: 37.64,
                name: '工业'
              },
              {
                value: 6.01,
                name: '服务业'
              }
            ]
          }
        ]
      },
      chart8: () => {
        const data = {
          '进口': createData(5, 14, 0.1, 0.05).map(n => +n.toFixed(2)),
          '出口': createData(5, 34, 0.1, 0.05).map(n => +n.toFixed(2))
        }
        return {
          legend: {
            x: 'center',
            y: 'top',
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'shadow'
            }
          },
          grid: {
            left: 20,
            right: 70,
            bottom: '3%',
            containLabel: true
          },
          yAxis: [
            {
              name: '亿美元',
              type: 'value',
            },
            {
              name: '增长率',
              type: 'value',
            }
          ],
          xAxis: {
            type: 'category',
            data: [
              '2015',
              '2016',
              '2017',
              '2018'
            ],
          },
          series: [
            {
              name: '进口',
              type: 'bar',
              barGap: 0,
              data: data['进口'].slice(1)
            },
            {
              name: '出口',
              type: 'bar',
              data: data['出口'].slice(1)
            },
            {
              name: '进口增长率',
              type: 'line',
              yAxisIndex: 1,
              data: resolveGrowthRate(data['进口'])
            },
            {
              name: '出口增长率',
              type: 'line',
              yAxisIndex: 1,
              data: resolveGrowthRate(data['出口'])
            }
          ]
        }
      }
    }
  },
}
</script>

<style lang="scss" scoped>
.table {
  table-layout: fixed;
  border-collapse: collapse;
  font-size: 14px;
  td,
  th {
    border: 1px #e8eaec solid;
    padding: 8px 16px;
    text-align: center;
  }
  th {
    font-size: 16px;
    background: #f8f8f9;
  }
}
</style>
